#priser-page{
    .hero{
        background: $light-white;
        .wrapper{
            padding: 10rem 0 5rem 0;
        }
    }
    .info-priser{
        padding:5rem 0;
        .row{
            h3{
                padding:4.1875rem 0;
            }
            p{
                padding-right:2rem;

            }
            .col-md-6{
                padding:0;
                margin-bottom:7.0625rem;
            }
            .pic{
                display: flex;
                justify-content: flex-end;

            }
        }
    }
    .contact-form{
        padding:10rem 0;
        .content{
            padding-right:15.3125rem;
            h2{
                margin-bottom: 5.625rem;
            }
            .iconss{
                border-radius:150px;
                background:$white;
                opacity:.48;
                display: flex;
                align-items: center;
                justify-content: center;
                padding:2rem 0;
                max-width:13.375rem;
                margin-right:15px;

                img{
                    width:8.82625rem;
                }
            }
            .icon-desc{
                color:$white;
                h4{
                    padding:2.4375rem 0;
                }
            }

        }
        .forms{
            padding-left:7.5rem;
            min-height:53rem;
            h2{
                max-width: 33.125rem;
                min-height:6.75rem;
            }
            form{
                padding:2.75rem 8.125rem 0 1.875rem;
                .row{
                    margin-bottom: .5rem;
                    .col{
                        padding: 0;
    
                        &:first-child{
                            margin-right:0.9375rem;
                        }
                    }
                    input.form-control,textarea{
                        background-color:transparent;
                        padding:0.9375rem;
                        color:$light-grey;
                        border-radius:10px;
                    }
                    &:nth-child(6){
                        margin-bottom: 1rem;
                    }
                }
                .display-13{
                    color:$grey;
                    padding-right:1rem;
                    margin-bottom: 1.8rem;
                }
            }
        }
    }
}