
.big-wrapper{
    nav{

        button.navbar-toggler{
            border-color:transparent;
        }
        #navbarSupportedContent{
            height:60px;
            margin-left: 9.6875rem;
            .navbar-nav{
                height: inherit;
                .nav-item{
                    .nav-link{
                        font-size: 15px;
                        color:$black;
                        height: 100%;
                        margin:0px 15px 0px 15px;
                        padding: 20px 0px 0px 0px;
                        height: 45px;
                        top: 0;
                    }
                }
            }
            .fr-begin-nav{
                .nav-item{
                    &:hover,&.active{
                        .nav-link{
                            border-bottom: 3px solid $brown;
                        }
                    }
                }
            }
            .fr-mid-nav{
                margin-right: 1.875rem;
                .nav-item{
                    .nav-link{
                        display: flex;
                        justify-content: center;
                        flex-direction: row;
                        align-items: center;
                        img{
                            margin-right:5px;
                        }
                    }
                    &:hover,&.active{
                        .nav-link{
                            span{
                                border-bottom: 3px solid $brown;
                            }
                        }
                    }
                    &.li-academy:hover{
                        img{
                            filter: invert(1);
                        }
                    }
                }

            }
            .fr-end-nav{
                .nav-item{
                    .nav-link{
                        &.dropdown-toggle.fr-language:after{
                            display: none;
                        }
                        &.dropdown-toggle.fr-language:before{
                            display: inline-block;
                            margin-left: .255em;
                            vertical-align: .255em;
                            content: "";
                            border-top: .3em solid;
                            border-right: .3em solid transparent;
                            border-bottom: 0;
                            border-left: .3em solid transparent;
                        }
                        i{
                            font-size: 1.375rem;
                        }
                    }
                    &:last-child{
                        .dropdown-menu{
                            left:auto;
                            right:0;
                        }
                    }
                    &:hover{
                        .nav-link.dropdown-toggle.fr-language:before{
                            border-top-color:$white;
                            border-bottom-color:$white;
                            filter:invert(0);
                        }
                    }
                }
            }
        }
    }
}
.nav-search{
    position: absolute;
    background: rgba(255,255,255,0.97);
    height:100vh;
    width:100%;
    z-index: 11;
    display: none;
    .big-wrapper{
        display: flex;
        justify-content: center;
        margin:auto;
        position: relative;

        form{
            margin-top:9.375rem;
            max-width:55rem;
            width: 55rem;
            .form-results{
                overflow-y: scroll;
                position: relative;
                height: 50vh;
            }
            .col-auto{
                padding:0px;
                    input{
                        color:$black;
                        background: 0 0;
                        border:none;
                        font-size:2.1875rem;
                        width:100%;
                        margin-bottom:0;
                        &:focus{
                            outline:none;
                        }
                        &::placeholder{
                            font-size:2.1875rem;
                        }
                    }
            }
            .card-long-v{
                padding:2.5rem 0 1.875rem 0;
                border-bottom: 2px solid $light-grey;
                .col-md-2.card-long-img{
                    justify-content: flex-start;
                }
                .col-md-2{
                    display: flex;
                    justify-content: flex-end;
                    align-items: center;
                    i{
                        font-size: 2rem;
                    }
                }
            }
            .form-navigator{
                .card-long-v{
                    border-bottom: 2px solid $dark-grey;
                }
                .col-md-2.card-long-img{
                    justify-content: center;
                }
            }
        }
        .close-v{
            position: absolute;
            right:0;
            top:3.74rem;
        }
    }

}