#reference-page{
    .hero{
        background: $light-white;
        .wrapper{
            padding: 10rem 0 5rem 0;
        }
    }
    .reference{
        padding:5.625rem 0px;
        .wrapper{
            .row{
                height:6.75rem;
                display: flex;
                justify-content: center;
                align-items: center;
                .col-sm-2{
                    width:100%;
                    height: inherit;
                    display: flex;
                    justify-content: center;
                    align-items: center;
                    max-height: 100%;
                    img{
                        width:auto;
                        max-height: inherit;
                    }
                }
            }
        }
    }
    .ref-lists{
        margin-bottom: 11.875rem;
    }
    .big-cta{
        height: 95vh;
        position: relative;
        .wrapper{
            height: inherit;
            display: flex;
            align-items: center;
         .text-wraps{
             max-width: 25.9375rem;
         }
        }
    }
}