$arrow-color: #e74c3c;
$arrow-size: 1rem;
$arrow-border: 0.1375rem;


@mixin fr_global_button($bg,$fg,$border,$border-color,$hover,$hover-fg){
    position: relative;
    display: block;
    width:max-content;
    line-height: 1;
    width: intrinsic;           /* Safari/WebKit uses a non-standard name */
      width: -moz-max-content;    /* Firefox/Gecko */
      width: -webkit-max-content; /* Chrome */
    padding: .9375rem ($arrow-size * 6.75) .9375rem 1.25rem;
    font-size: 1rem;
    text-decoration: none;
    border-radius:30px;
    transition: all 0.2s ease-in-out;
    //box-shadow: inset 0 0 0 $arrow-border $arrow-color;
    
    &:after, &:before {
      content: "";
      display: block;
      position: absolute;
    }
    &:before {
      top: 50%;
      right: 1.05rem;
      width: 0.55625rem;
      height: 0.55625rem;
      transform-origin: 0% 50%;
      transform:  translate(0, -($arrow-size/2.1)) rotate(45deg);
      border-radius: 0 $arrow-size*0.15 0 0;
    }
    
    &:after {
      top: 50%;
      right: 1.25rem;
      width: 1.875rem;
      height: 0.09375rem;
      transform:  translate(0, -($arrow-border / 2));
      background-color: $arrow-color;
    }
    @if($hover != "none"){
        &:hover{
            padding: .9375rem ($arrow-size * 9.75) .9375rem 1.25rem;
            transition: all 0.2s ease-in-out;
        }
    }
        @if($border == "none"){
            background:$bg;
            border: 1.5px solid $bg;

            @if($bg == $black){
                color:$fg;
                &:before {
                    box-shadow: inset -#{$arrow-border} $arrow-border 0 0 $fg;
                }
                &:after {
                    background-color: $fg;
                }
                @if($hover == $brown)
                {
                    &:hover {
                        color:$white;
                        background-color:$hover;
                        border-color:$hover;
                    &:before {
                        box-shadow: inset -#{$arrow-border} $arrow-border 0 0 $white;
                    }
                    &:after {
                        background-color: $white;
                    }
                } 
                }
                @else if($hover == "none"){}
                @else{
                    &:hover {
                        color:$black;
                        background-color:$hover;
                        border-color:$hover;
                    &:before {
                        box-shadow: inset -#{$arrow-border} $arrow-border 0 0 $black;
                    }
                    &:after {
                        background-color: $black;
                    }
                } 
                }  
            }
            @else if($bg == $white)
            {
                color:$white;
                &:before {
                    box-shadow: inset -#{$arrow-border} $arrow-border 0 0 $white;
                }
                &:after {
                    background-color: $white;
                }
                @if($hover != "none"){
                    &:hover {
                        color:$gold;
                    &:before {
                        box-shadow: inset -#{$arrow-border} $arrow-border 0 0 $gold;
                    }
                    &:after {
                        background-color: $gold;
                    }
                }
            }
            }
            @else if($bg == $brown)
            {   
                color:$fg;
                &:before {
                    box-shadow: inset -#{$arrow-border} $arrow-border 0 0 $fg;
                }
                &:after {
                    background-color: $fg;
                }
                @if($hover != "none"){
                    &:hover {
                        color:$hover-fg;
                        background:$hover;
                        border-color:$hover;
                    &:before {
                        box-shadow: inset -#{$arrow-border} $arrow-border 0 0 $hover-fg;
                    }
                    &:after {
                        background-color: $hover-fg;
                    }
                }
            }
            } 
            @else if($bg == $grey)
            {   
                color:$black !important;
                &:before {
                    box-shadow: inset -#{$arrow-border} $arrow-border 0 0 $black;
                }
                &:after {
                    background-color: $black !important;
                }
                @if($hover != "none"){
                    &:hover {
                        color:$white !important;
                    &:before {
                        box-shadow: inset -#{$arrow-border} $arrow-border 0 0 $white !important;
                    }
                    &:after {
                        background-color: $white !important;
                    }
                }
            }
            }   
            @else{
                color: $fg !important;
                &:before {
                    box-shadow: inset -#{$arrow-border} $arrow-border 0 0 $fg;
                }
                &:after {
                    background-color: $fg;
                }
                @if($hover != "none"){
                    &:hover {
                        color:$hover-fg !important;
                        background: $hover;
                        border-color:$hover;
                    &:before {
                        box-shadow: inset -#{$arrow-border} $arrow-border 0 0 $hover-fg !important;
                    }
                    &:after {
                        background-color: $hover-fg !important;
                    }
                }
            }
            }   
        }
        @else{
            background: transparent;
            border: 1.5px solid $border-color;
            @if($border-color == $brown)
            {
                color:$fg;
                &:before {
                    box-shadow: inset -#{$arrow-border} $arrow-border 0 0 $brown;
                }
                &:after {
                    background-color: $brown;
                }
                @if($hover != "none"){
                    &:hover {
                        color:$black;
                        border-color:$black;
                        &:before {
                            box-shadow: inset -#{$arrow-border} $arrow-border 0 0 $black;
                        }
                        &:after {
                            background-color: $black;
                        }
                    }
                } 
            }
        }

}

