            content: url('../img/cloud/play.png');
            width: 3.91375rem;
            height: 4.9225rem;
            top:calc(50% - (4.9225rem / 2));
            left:calc(50% - (3.91375rem / 2));
              position: absolute;
        }

    }
    &:hover{
        .vid-overlay{
            .fr-vid-play-button{
                  content: url('../img/cloud/play_h.png');
            }
        }
    }
  }
