    position: relative;
  }
label, input, textarea {
    transition: all 0.2s;
    touch-action: manipulation;
  }

  input,textarea{
    font-size: 1.125rem;
    border: 0;
    -webkit-appearance: none;
    border-radius: 10px;
    border:1.5px solid #666;
    padding: 0;
    cursor: text;
    margin-bottom: 2rem;
  }
  input:focus,textarea:focus {
    outline: transparent !important;
    box-shadow:none !important ;
    border-color:#999;
  }
  
  label {
    letter-spacing: 0.05em;
    position: absolute;
  }
  /**
  * Translate down and scale the label up to cover the placeholder,
  * when following an input (with placeholder-shown support).
  * Also make sure the label is only on one row, at max 2/3rds of the
  * fieldto make sure it scales properly and doesn't wrap.
  */
  input:placeholder-shown + label,textarea:placeholder-shown + label {
    cursor: text;
    max-width: 66.66%;
    white-space: nowrap;
    overflow: hidden;
    font-size: 1.125rem;
    text-overflow: ellipsis;
    color:#999;
    top: .5rem;
    left: 0.8rem;
  }
  /**
  * By default, the placeholder should be transparent. Also, it should 
  * inherit the transition.
  */
  ::-webkit-input-placeholder {
    visibility: hidden;
    transition: inherit;
  }
  :-ms-input-placeholder { /* Internet Explorer 10-11 */
    visibility: hidden;
    transition: inherit;

  }
  
  ::placeholder {
    visibility: hidden;
    font-size: 1.125rem;
    transition: inherit;

  }
  /**
  * Show the placeholder when the input is focused.
  */
  input:focus::-webkit-input-placeholder {
    visibility:visible;
  }
  /**
  * When the element is focused, remove the label transform.
  * Also, do this when the placeholder is _not_ shown, i.e. when 
  * there's something in the input at all.
  */
  input:not(:placeholder-shown) + label,input:not(:placeholder-shown),
  input:focus + label ,textarea:focus + label,label{
    top:-1.5rem;
    left:0rem;
    font-size: 1rem;
    color:#999;
    border-color:#999;
  }
