  font-style: normal;
}
/*Work sans*/
@import url('https://fonts.googleapis.com/css?family=Work+Sans&display=swap&subset=latin-ext');
@import url("https://maxcdn.bootstrapcdn.com/font-awesome/4.5.0/css/font-awesome.min.css");

/* Typography */
body {
  font-family: 'Mark-Pro-Medium';
  overflow-x: hidden;
}
.mark-book{
font-family: 'Mark-Book';
}
.mark-regular{
  font-family: 'Mark-Pro';
}
.mark-italic{
font-family: 'Mark-Regular-Italic';
}
.work-sans{
font-family: 'Work Sans', sans-serif;
}

/*Convertion Ration sp to rem for html */
$conversionRatioSpToRemWeb: 1/16; //! 0.0625;
$conversionRatioRemToPxWeb: 16.0; 
// Scales
@function spToRem($sps){
@return $sps * $conversionRatioSpToRemWeb;
};
@function remToPx($rems){
@return $rems * $conversionRatioRemToPxWeb;
};
@function spacing($tracking, $px){
@return $tracking / $px;
};
@mixin mat-typo($size, $weight, $spacing, $lineheight) {
font-weight:  $weight;
font-size: #{spToRem($size)}rem;
letter-spacing: #{spacing($spacing, remToPx($size))}px;
line-height: $lineheight+px;
}
// Weights
$font-weight-thin:    100;
$font-weight-light:   300;
$font-weight-regular: 400;
$font-weight-medium:  500;
$font-weight-semibold:  600;
$font-weight-bold:    700;
$font-weight-black:   900;

.f-w-6{
