#contact-page{
    .contact-form{
        padding:10rem 0;
        h2{
            // margin-bottom: 8.125rem;
            margin-bottom: 9rem;
            span{
                margin-left: 9.0625rem;
            }
        }
        .content{
            .f-content{
                padding-right:14.3125rem;
            }
            h2{
                margin-bottom: 5.625rem;
            }
            .iconss{
                border-radius:150px;
                background:$white;
                opacity:.48;
                display: flex;
                align-items: center;
                justify-content: center;
                padding:2rem 0;
                max-width:13.375rem;
                max-height:13.375rem;
                margin-right:15px;
                // height: min-content;
                img{
                    width:8.82625rem;

                }
            }
            .icon-desc{
                color:$white;
                h4{
                    padding:2.4375rem 0;
                }
            }
            .grid-contain{
                display: grid;
                margin-top:4.6875rem;
                grid-template-columns: auto auto auto;
                .grid-item{
                    color:$white;
                    display: flex;
                    flex-direction: column;
                    justify-content: center;
                    align-items: center;
                    a{
                        display: flex;
                        flex-direction: column;
                        align-items: center;
                        min-width: 145px;
                        span{
                            width: 9.375rem;
                            border-radius: 100px;
                            display: flex;
                            align-items: center;
                            justify-content: center;
                            height: 9.375rem;
                            margin-top:1.875rem;
                            background: rgba(204, 204, 204, 0.57);
                            i{
                                color:#000;
                                font-size: 5rem;
                            }
                        }
                    }
                    img{
                        margin-top:1.875rem;
                        max-width:9.375rem;
                        max-height:9.375rem;
                    }
                }
            }
        }
        .forms{
            padding-left:7.5rem;
            h2{
                max-width: 33.125rem;
            }
            form{
                padding:2.75rem 8.125rem 0 1.875rem;
                .row{
                    margin-bottom: 2.75rem;
                    .col{
                        padding: 0;
    
                        &:first-child{
                            padding-right:0.9375rem;
                        }
                    }
                    input.form-control,textarea{
                        background-color:transparent;
                        border:1.5px solid $light-grey;
                        padding:0.9375rem;
                        color:$light-grey;
                        border-radius:10px;
                    }
                    &:nth-child(6){
                        margin-bottom: 1rem;
                    }
                }
                .display-13{
                    color:$grey;
                    padding-right:1rem;
                    margin-bottom: 1.8rem;
                }
            }
        }
    }
}