#case-page{
    .hero{
        background: $light-white;
        .wrapper{
            padding: 10rem 0 5rem 0;
        }
    }
    .news-row{
        background: $light-white;
        padding-bottom: 5.375rem;
        .wrapper{
            .row{
                display: flex;
                margin-bottom: 5rem;
                img{
                    width:auto;
                    padding-bottom: 1.25rem;
                }
                h1{
                    padding: 1.25rem;
                }
                .single-news-left{
                    display: flex;
                    flex:1;
                    flex-direction: column;
                    align-items: flex-start;
                    overflow: hidden;
                    margin-right:5%;
                    img{
                        float: left;
                        max-width: 100%;
                    }

                }
                .single-news-right{
                    display: flex;
                    flex-direction: column;
                    align-items: flex-end;
                    overflow: hidden;
                    img{
                        float: right;
                        max-width: 100%;
                    }
                    h1{
                        width: 100%;
                    }
                }
                &.r-rev{
                    .single-news-left{
                        margin-right:10%;
                    } 
                }
            }
        }

    }
    .testimon{
        padding-top: 6.25rem;
        .row{
            padding-top:8.125rem;
            padding-bottom: 11.875rem;
            .col-md-6{
                text-align: center;
                display: flex;
                flex-direction: column;
                align-items: center;
                p.display-5{
                    padding-bottom: 3.375rem ;
                    max-width: 35rem;;
                    min-height: 15rem;
                }
            }
        }
    }
    .reference{
        background: $light-white;
        padding:5.625rem 0px;
        .wrapper{
            .row{
                height:6.75rem;
                display: flex;
                justify-content: center;
                align-items: center;
                .col-sm-2{
                    width:100%;
                    height: inherit;
                    display: flex;
                    justify-content: center;
                    align-items: center;
                    max-height: 100%;
                    img{
                        width:auto;
                        max-height: inherit;
                    }
                }
                &.button-center{
                    justify-content: flex-start;
                    margin-left: 45%;
                }
            }
        }
    }
}