#backline-page{
    background:#333;
    .small-hero{
        .top{
            padding:10rem 0 17rem 0;
            h1{
                padding-bottom:3.5rem;
                line-height: 1;
            }
            p{
                padding-right: 4rem;
            }
        }
    }
    .list-grids{
        padding-top:10rem;
        h1{
            margin-bottom: 1.875rem;
        }
        ul{
            margin-top:1.875rem;
            li{
                margin-bottom: 1.875rem;
            }
        }
    }
    .grid-info{
        .wrapper{

            &.g-top{
                padding:0 0;
            }
            &.g-bot{
                padding:0rem 0 10rem 0;
            }
            h1{
                padding-bottom:3.5rem;
                line-height: 1;
            }
            p{
                padding-right: 4rem;
            }
            .topics{
                padding-top:6.875rem;
            }
            .bot{
                padding-top:3.75rem;
                .col-lg-4{
                    margin-bottom: 1.875rem;
                    padding-left:0.9375rem;
                    padding-right:0.9375rem;
                    img{
                        width:100%;
                    }
                    .display-1{
                        padding:1.375rem;
                        width:100%;
                        background:$black;
                    }
                    &:hover{
                        .display-1{
                            background:$brown;
                            color:$black;
                            span{
                                color:$white !important;
                            }
                        }
                    }
                }
            }
        }
    }
}