#aboutus-page {
    @mixin on-circle($item-count, $circle-size, $item-size) {  
        position: relative;
        width:  $circle-size;
        height: $circle-size;
        border-radius: 50%;
        padding: 0; 
        list-style: none;
        
        > * {
          display: block;
          position: absolute;
          top:  50%; 
          left: 50%;
          margin: -($item-size / 2);
          width:  $item-size;
          height: $item-size;
        
          $angle: (360 / $item-count);
          $rot: 0;
      
          @for $i from 1 through $item-count {
            &:nth-of-type(#{$i}) {
              transform: rotate($rot * 1deg) translate($circle-size / 2) rotate($rot * -1deg);
            }
      
            $rot: $rot + $angle;
          }
        }
      }

      @mixin on-circles($item-count, $circle-size, $item-size) {  
        position: relative;
        width:  $circle-size;
        height: $circle-size;
        border-radius: 50%;
        padding: 0; 
        list-style: none;
        
        > * {
          display: block;
          position: absolute;
          top:  50%; 
          left: 50%;
          margin: -($item-size / 2);
          width:  $item-size;
          height: $item-size;
        
          $angle: (360 / $item-count);
          $rot: 0;
          $first: $angle;
          @for $i from 1 through $item-count {
            &:nth-of-type(#{$i}) {
              transform: rotate($rot * 1deg) translate($circle-size / 2) rotate($rot * -1deg);
              svg{
                  transform: rotate($first + $angle + 0deg);
              }
            }
            $first: $first + $angle;
            $rot: $rot + $angle;
          }
        }
      }
      .circle-containers {
        @include on-circles($item-count: 7, $circle-size: 18em, $item-size: 7em); 
        position: absolute;
        top:5rem;
        left:8rem;
    }
      .circle-container {
        @include on-circle($item-count: 7, $circle-size: 30em, $item-size: 7em); 
        display: flex;
        /* align-items: center; */
        justify-content: center;
        p{
            position: relative;
            color:$white;
        }
        img { 
          display: block; 
          max-width: 100%; 
          border-radius: 50%;
          filter: grayscale(100%);
          border: solid 5px tomato;
          transition: .15s;
           
          &:hover {
            filter: grayscale(0);
          }
        }
      }
    .hero{
        background: $light-white;
        .wrapper{
            padding: 10rem 0 5rem 0;
        }
    }
    .two-sec-rev{
        padding:5.125rem 0;
        position: relative;
        .page-desc{
            padding:0;
                .mag-wrap{
                    max-width:36.3125rem;
                    margin-left:auto;
                    h1{
                        padding-bottom:3.125rem;
                    }
                    p{
                        padding-top:2.5125rem;
                    }
                    a{
                        margin-top:1.875rem;
                    }
                }
        }
        .page-pic{
            display: flex;
            align-items: flex-start;
            padding:0;
            z-index: 1;
            img{
                width:100%;
                height:auto;
                max-width: 55rem;
            }
        }
        .back-image{
            position: absolute;
            bottom: 0;
            left:-50px;
        }
    }
    .circle-sec{
        padding:12.25rem 0;
        background:$black;
        .col-md-5{
            padding-left:9.6875rem;
            padding-right:7.8125rem;
            h1{
                padding-bottom: 2.8125rem;
            }
        }
        .col-md-7{
            display:flex;
            justify-content: center;
        }
    }
    .tal{
        padding:6.25rem 0;
        .prices{
            padding-right:11.25rem;
            display: flex;
            flex-direction: column;
            align-items: flex-end;

            h3{
                font-size: 5rem;
                margin-bottom: 1.875rem;
            }
            div.display-10{
                color:$green;
                margin-bottom: 3.125rem;
                display: flex;
                justify-content: flex-end;
                align-items: center;
                img{
                    margin-left:5px;
                }
            }
        }
        .info{
            display: flex;
            flex-direction: column;
            justify-content: center;
            h1{
                margin-bottom: 3.4375rem;
            }
            p{
                max-width: 26.125rem;
            }
        }
    }
    .hip{
        background: $brown;
        padding:12.5rem 0px;
        display: flex;
        flex-direction: row;
        justify-content: center;
        h1{
            margin-right: 1.875rem;
        }
    }
    .globe{
        .c-wrap{
            width:71.5625rem;
            margin:auto;
            padding:15rem 0 5rem 0;
            h1{
                margin-bottom: 3.4375rem;
            }
            h2{
                margin-bottom: 10rem;
            }
            .g-step{
                margin-top:1rem;
                margin-bottom: 5rem;
                .col-md-4{
                    display: flex;
                    justify-content: center;
                    align-items: center;
                }
                h3{
                    margin-bottom: 1.875rem;
                }

            }
        }
    }
    .big-cta{
        height: 95vh;
        position: relative;
        .wrapper{
            height: inherit;
            display: flex;
            align-items: center;
         .text-wraps{
             max-width: 25.9375rem;
         }
        }
    }
}