#contactB-page{
    .contact-form{
        padding:10rem 0;
        .content{
            padding-right:15.3125rem;
            // padding-top:12.125rem;
            padding-top: 12.4rem;
            h2{
                margin-bottom: 5.625rem;
            }
            .iconss{
                border-radius:150px;
                background:$white;
                opacity:.48;
                display: flex;
                align-items: center;
                justify-content: center;
                padding:2rem 0;
                max-width:13.375rem;
                margin-right:15px;

                img{
                    width:8.82625rem;
                }
            }
            .icon-desc{
                color:$white;
                h4{
                    padding:2.4375rem 0;
                }
            }

        }
        .forms{
            padding-left:7.5rem;
            min-height:43rem;
            h2{
                max-width: 33.125rem;
                margin-bottom: 5.625rem;
                min-height:6.75rem;
            }
            h3{
                margin-bottom:1.875rem;
                padding-right:8.125rem;
            }
            p{
                margin:0;
                padding-right:8.125rem;
            }
            form{
                padding:2.75rem 8.125rem 0 0;
                .row{
                    margin-bottom: 2.75rem;
                    .col{
                        padding: 0;
    
                        &:first-child{
                            padding-right:0.9375rem;
                        }
                    }
                    input.form-control,textarea{
                        background-color:transparent;
                        border:1.5px solid $light-grey;
                        padding:0.9375rem;
                        color:$light-grey;
                        border-radius:10px;
                    }
                    &:nth-child(6){
                        margin-bottom: 1rem;
                    }
                }
                .display-13{
                    color:$grey;
                    padding-right:1rem;
                    margin-bottom: 1.8rem;
                }
            }
        }
    }
}