#support-page{
    .hero{
        background: $light-white;
        .wrapper{
            padding: 10rem 0 5rem 0;
        }
    }
    .header-image{
        img{
            width:100%;
        }
    }
    .call{
        padding:8.125rem 0 13.4375rem 0;
        .left{
            .combo{
                margin-top:4.0625rem;
                margin-bottom: 1.25rem

            }
            p.display-2{
                margin-bottom: 2rem;
                max-width: 25.5rem;
            }
            .grid-contain{
                max-width: 19.375rem;
                display: grid;
                margin-top:1.375rem;
                grid-template-columns: auto auto;
            }
        }
        .content{
            display: flex;
            flex-direction: column;
            justify-content: center;
            max-width: 23.4375rem;
            a{
                margin-top:2.0625rem;
            }
            .forms{
                input.form-control,textarea{
                    background-color:transparent;
                    padding:0.9375rem;
                    font-size: 1.125rem;
                    color:$black;
                    border-radius:10px;
                    border:1.5px solid #999;
                }
                .bs-main{
                    margin-bottom:1.25rem;
                }
                .form-titles{
                    color:#000 !important;
                }
            }
        }
    }
    .two-sec2{
        padding:5.125rem 0;
        background: $light-white;
        .page-desc{
            padding:0;
                .mag-wrap{
                    max-width:26.3125rem;
                    margin-right:auto;
                    h1{
                        padding-bottom:2.8125rem;
                    }
                    .display-2{
                        padding-right:1.5rem;
                    }
                }
        }
        .page-pic{
            display: flex;
            justify-content: flex-end;
            align-items: flex-start;
            padding:0;
            margin-top:1.25rem;
            img{
                width:100%;
                max-width:51.625rem;
                height:auto;
            }
        }
    }
    .accord{
        background: $light-white;
        .row{
            .col-md-6{
                padding:0;
            }
            .pics{
                display: flex;
                align-items: flex-start;
                justify-content: flex-end;
                padding-bottom: 9.375rem;
                padding-top:2rem;
                padding-left:8.125rem;
                img{
                    width:100%;
                }
            }
            .accordion{
                padding-top:4.0625rem;
                padding-right:11.25rem;
                min-height:50rem;
                .card{
                    border:none;
                    border-radius:10px;
                    margin-bottom: 10px;
                    min-height:6.125rem;
                    .card-header{
                        background:$white;
                        border:none;

                        button{
                            // position: absolute;
                            // top: 0.5rem;
                            // right: 2%;
                            width:100%;
                            text-decoration: none;
                            text-align: left;
                            color:#000;
                            &:before{
                                float: right !important;
                                font-family: FontAwesome;
                                content:"\f068";
                                padding-left: 15px;
                            }
                            &.collapsed:before {
                                float: right !important;
                                font-family: FontAwesome;
                                content:"\f067";
                            }
                        }
                    }
                    .card-body{
                        font-size: 1rem;
                    }
                }

            }
        }
    }
}