#home {
    a.carousel-control-prev,a.carousel-control-next{
        left:auto;
        top:auto;
        bottom: 65px;
        display: flex;
        opacity: 1;
        position: absolute;
    }
    a.carousel-control-prev{
        right:7.1875rem;
        left:auto;
    }
    a.carousel-control-next{
        right:3.4375rem;
    }
   .hero{
        height:calc(100vh - 60px);
        .carousel{
            height: inherit;
            .carousel-inner{
                height: inherit;
                .carousel-item{
                    height: inherit;
                    background: #fff;
                    .carousel-caption{
                        text-align: left;
                        left:50%;
                        bottom: 0;
                        margin-left: -46.875rem !important;
                        .col-md-7{
                            position: absolute;
                            bottom: 0;
                            right: 0;
                        }
                    }
                    img{
                        width:120%;
                    }
                }
            }
        }
         .wrapper{
             height: inherit;
             .row{
                 height: 100%;
                 .hero-left{
                     flex-direction: column;
                    justify-content: center;
                     p{
                         max-width: 31.9375rem;
                     }
                 }
                 .hero-right{
                     display: flex;
                     align-items: flex-end;
                 }
             }
         }
   }
   .features{
       height: 95vh;
       .big-wrapper{
           height: inherit;
           padding:60px 0px;
           .accordion{
                .card{
                    .card-header{
                        background:rgba(0,0,0,.3);
                        button{
                            width:100%;
                            text-decoration: none;
                            text-align: left;
                            color:#000;
                            &:before{
                                float: right !important;
                                font-family: FontAwesome;
                                content:"\f068";
                                padding-right: 5px;
                            }
                            &.collapsed:before {
                                float: right !important;
                                font-family: FontAwesome;
                                content:"\f067";
                            }
                        }
                    }
                    .card-body{
                        padding:3rem;
                    }
                }
           }
           .row{
               height: 100%;
               display: flex;
               flex-direction: row;
               flex-wrap: nowrap;
               .col-md-3{
                   height: inherit;
                   overflow: hidden;
                   display: flex;
                   align-items: center;
                   justify-content: center;
                   margin: 0px 1.5px;
                   padding:0px;
                   img{
                       margin-top:100px;
                     margin-left: -400px;
                   }
                   &:first-child img{
                       margin-top: 0px;
                       margin-left: 0px;
                   }
                   &:nth-child(2) img{
                    margin-left: -300px;
                }
                   h1{
                       position: absolute;
                       bottom: 60px;
                       left:50px;
                       color:$white;
                   }
               }
           }
       }
   }
   .big-cta{
       height: 95vh;
       position: relative;
       .wrapper{
           height: inherit;
           display: flex;
           align-items: center;
        .text-wraps{
            max-width: 25.9375rem;
        }
       }
   }
   .umbrella{
       height: 70.6875rem;
       position: relative;
       .wrapper{
           height: inherit;
           padding-top:9.375rem;
           .row{
               height: 100%;
               .col-md-6{
                display: flex;

                flex-direction: column;
                   &.f-left{
                    padding-top:9.375rem;
                   }
                   &.f-right{
                       align-items: center;
                       
                       div{
                           img{
                            position: absolute;
                            top: 0;
                            right: 0;
                            width: 9.6875rem;
                           }
                        p{
                            padding-top:24.1875rem;
                            max-width: 25.9375rem;
                           text-align: left;
                           line-height: 1.5;
                       }                         
                       }

                   }
               }
           }
       }
       img.d-image{
        position: absolute;
        bottom: 50px;
        left: 0;
        width: 20rem;
        margin-left: -9.375rem;
       }
   }
   .cloud{
       margin-bottom: 9.0625rem;
       .big-wrapper{
           .row{
               .col-md-6{
                padding:8.125rem;
               }
           }
       }
   }
   .cta{
       background: $light-white;
       margin-bottom: 50px;
       .carousel{
           height: inherit;
           .carousel-inner{
            padding:9.0625rem 0px;
            .hero-left{
                display: flex;
                align-items: center;
                flex-direction: column;
             p{
                 max-width:74.5rem;
             }  
           }
        }
       }
   }
   .nyhed{
       padding: 6.25rem 0px;
       margin-bottom: 2.8125rem;
       .row{
            .nyhed-wrapper{
                padding:0px;
                overflow: hidden;
                img{
                    width:100%;
                }
            }
           .col-md-6.f-right{
               padding-left:12.5rem;
           }
       }
   }
   .vision{
    background: $light-white;
       padding:6.25rem 0px;
       img{
           width:100%;
       }
   }
   .hip{
       background: $brown;
       padding:12.5rem 0px;
       display: flex;
       flex-direction: row;
       justify-content: center;
       h1{
           margin-right: 1.875rem;
       }
   }
   .kunde{
    background: $light-white;
       padding:6.25rem 0px;
       img{
           width:100%;
       }
   }
   .cta-small{
       //padding:6.25rem 0px;
       padding:9.0625rem 0;
       p{
        max-width: 71rem;
        /* text-align: center; */
        margin-left: auto;
        margin-right: auto;
       }
   }
   .reference{
       background: $light-white;
       padding:5.625rem 0px;
       .wrapper{
           .row{
               height:6.75rem;
               display: flex;
               justify-content: center;
               align-items: center;
               .col-sm-2{
                   width:100%;
                   height: inherit;
                   display: flex;
                   justify-content: center;
                   align-items: center;
                   max-height: 100%;
                   img{
                       width:auto;
                       max-height: inherit;
                   }
               }
               &.button-center{
                   justify-content: flex-start;
                   margin-left: 45%;
               }
           }
       }
   }
}