@mixin location_click($top,$left){
    position: absolute;
    top: $top;
    left: $left;   
}
@mixin location_info($top,$left){
    margin-top: $top;
    margin-left: $left;   
}
@keyframes my{
    0% {transform: scale(1);
        opacity:50%;}
    50%  {transform: scale(1.2);
        opacity:80%;}
    100% {transform: scale(1);
            opacity:50%;}
}
#cloud-page{
    .grey-part{
        width:100%;
        background:#f3f3f3;
        height:15rem;
    }
    .hero{
        position: absolute;
        z-index: 10;
        width: 100%;
        background: linear-gradient(180deg, #fff, transparent);
        .wrapper{
            padding: 10rem 0 5rem 0;
        }
    }
    .click-on{
        height: 62vw;
        position: relative;
        .clicker{
            img{
                width:1.5625rem;
                height: auto;
                cursor: pointer;
            }
            .c-info{
                display: none;
                width:18.4375rem;
                padding:2.125rem;
                background: $white;
                border-radius:10px;
                position: relative;
                z-index: 1;
                i{
                    float:right;
                    cursor: pointer;
                }
                img{
                    margin-bottom: 1.0625rem;
                }
                h2{
                    margin-bottom: 0.3125rem;
                }
                h3{
                    margin-bottom: 1.875rem;
                }
                &.active{
                    display: block;
                }
            }
            &.hov:hover{
                animation: my 2s infinite;
            }
        }
        .c1{
            @include location_click(33vw,33vw);
            .c-info{
                @include location_info(-10vw,50px);
            }
        }
        .c4{
            @include location_click(27vw,60vw);
            .c-info{
                @include location_info(-10vw,50px);
            }
        }
        .c5{
            @include location_click(29vw,70vw);
            .c-info{
                @include location_info(-10vw,-20rem);
            }
        }
        .c6{
            @include location_click(29.5vw,84vw);
            .c-info{
                @include location_info(-10vw,-20rem);
            }
        }
        .c7{
            @include location_click(29.5vw,88vw);
            .c-info{
                @include location_info(-10vw,-20rem);
            }
        }
        .c8{
            @include location_click(40vw,80vw);
            .c-info{
                @include location_info(-10vw,-20rem);
            }
        }
    }
    .two-sec{
        padding:9.375rem 0;
        .page-desc{
            padding:0;
                .mag-wrap{
                    width:29.0625rem;
                    h1{
                        padding-bottom:3.125rem;
                    }
                    p{
                        padding-top:2.5125rem;
                    }
                    .display-2{
                        padding-right:2rem;
                    }
                }
        }
        .page-pic{
            display: flex;
            align-items: center;
            justify-content: flex-end;
            padding:0;
            position:relative;
            video{
                max-width:51.625rem;
                width:100%;
                height:auto;
            }
            #controls{
                position:absolute;
                left:57%;
            }
        }
    }
    .tabbed{
        background:$green;
        padding:7.1875rem 0;
        h1{
            margin-bottom: 4.0625rem;
        }
        .tabbed-fn{
            padding:6.25rem 10.625rem 0 10.625rem;
            border:none;
            li{
                width:12.5%;
                margin-bottom: 3.125rem;
                a{
                    margin:auto;
                    max-width:100px;
                    display: flex;
                    flex-direction: column;
                    align-items: center;
                    opacity: .5;
                    span{
                        margin-top:0.625rem;
                        text-align: center;
                        color:$black;
                        font-family: 'Work Sans', sans-serif;
                    }
                    &.active{
                        img{
                            filter:grayscale(1);
                        }
                        opacity: 1;
                    }
                }

            }
        }
        .tab-content{
            margin:1.875rem 0;
            padding:0 10.625rem;
            min-height: 15rem;

            .col-md-3{
                display: flex;
                justify-content: center;
                img{
                    width:100%;
                    max-width:200px;
                    height:auto;
                }
            }

            h3{
                margin-bottom: 2.5rem; 
            }
        }
    }
    .some-info{
        padding:8.4375rem 0;
        .row{
            padding: 4.375rem 0;
            .col-md-6:first-child{
                padding-left: 0;
            }
            .col-md-6:last-child{
                padding-right: 0;
            }
            p{
                max-width:570px;
            }
        }
    }
    .big-cta{
        height: 95vh;
        position: relative;
        .wrapper{
            height: inherit;
            display: flex;
            align-items: center;
         .text-wraps{
             max-width: 25.9375rem;
         }
        }
    }
}