#service-page{
    .hero{
        background: $light-white;
        .wrapper{
            padding: 10rem 0 5rem 0;
        }
    }
    .only-header{
        position: relative;
        background-size: cover;
        .wrapper{
            padding: 10rem 0 10rem 0;
            position: relative;
            h1{
                padding-bottom: 5rem;
            }
            p{
                max-width:45rem;
                color:$white;
            }
        }   
    }
    .grid-info{
        background:$light-grey;
        .wrapper{
            padding:8rem 0;
            h1{
                padding-bottom:3.5rem;
                line-height: 1;
            }

            .bot{
                padding-top:6.875rem;
                .wrap{
                    .links{
                        padding-right:5rem;
                        h3.fg-brown{
                            margin-bottom:3rem;
                        }
                        ul{
                            margin-top: 1rem;
                        }
                    }
                }
            }
        }
    }
    .page-50{
        background:$dark-greys;
        position: relative;
        height:45rem;
        .wrapper{
            height: inherit;
            .row{
                height: inherit;
                justify-content: flex-end;
                .page-desc{
                    height: inherit;
                    display: flex;
                    flex-direction: column;
                    justify-content: center;
                    padding-left:21rem;
                    h1{
                        padding-bottom:3.4375rem;
                    }
                    a{
                        color:$black;
                    }
                    ul{
                        list-style: none;
                        margin: 0;
                        li{
                            display: flex;
                            flex-direction: row;
                            span{
                                margin-right:3rem;
                                display: flex;
                                align-items: center;
                                img{
                                    width:35px;
                                    height:35px;
                                }
                            }
                            p{
                                margin:0;
                                line-height: 2;
                                font-weight: 600;
                                display: flex;
                                align-items: center;
                                a{
                                    -webkit-transition: all 0.5s ease;
                                }
                            }
                            margin-bottom: 2rem;
                            &:last-child{
                                margin-bottom: 0;
                            }
                        }
                    }
                }
                .page-pic{
                    position: absolute;
                    left: 0;
                    top: 0;
                    padding: 0;
                    overflow: hidden;
                    img{
                        float: left;
                        transform: scale(1.5);
                    }
                }
            }
        }
    }
    .magazine{
        .wrapper{
            padding-top:12.5rem;
            padding-bottom:5.625rem;
            .left{
                img{
                    margin-bottom:3.125rem;
                }
            }
            .right{
                .mag-wrap{
                    width:35.625rem;
                    h4,h1{
                        padding-bottom:3.125rem;
                    }
                    .wrap.w-cols{
                        border-spacing:2rem;
                    }
                    .h5{
                        margin-top:2.5125rem;
                    }
                    .display-2{
                        padding-right:2rem;
                    }
                }
            }
        }
    }
    .blog-list{
        .wrapper{
            padding-top:6.875rem;
            padding-bottom:9.6875rem;
            .row{
                margin-bottom: 4.375rem;
                .desc-wrap{
                    .blog-desc{
                        width:29.0625rem;
                        padding-top:3.75rem;
                        p{
                            padding-top:3.125rem;
                            padding-right: 1.1rem;
                        }
                    }
                }
            }
        }
    }
    .big-cta{
        height: 95vh;
        position: relative;
        .wrapper{
            height: inherit;
            display: flex;
            align-items: center;
         .text-wraps{
             max-width: 25.9375rem;
         }
        }
    }
}