#news-page{
    .hero{
        background: $light-white;
        .wrapper{
            padding: 10rem 0 5rem 0;
        }
    }
    .news-row{
        padding-top:10rem;
        background: $light-white;
        .wrapper{
            .row{
                display: flex;
                padding-bottom: 5rem;
                img{
                    width:auto;
                    padding-bottom: 1.25rem;
                }
                h1{
                    padding: 1.25rem;
                }
                .single-news-left{
                    display: flex;
                    flex:1;
                    flex-direction: column;
                    align-items: flex-start;
                    overflow: hidden;
                    img{
                        float: left;
                    }

                }
                .single-news-right{
                    display: flex;
                    flex-direction: column;
                    align-items: flex-end;
                    overflow: hidden;
                    img{
                        float: right;
                    }
                    h1{
                        width: 100%;
                    }
                }
            }
        }

    }
}