#backline-inner-page{
    background: #333333;
    .small-hero{
        .text-in{
            padding:18.25rem 0;
            p{
                padding-top:1.875rem;
                max-width: 22.5rem;
            }
        }
    }
    .magazine-rev{
        .big-wrapper{
            padding:5.625rem 0;
            .left{
                position: absolute;
                right:0;
                .row{
                    .col-md-6:first-child{
                        padding-right:1.5625rem;
                    }
                    .col-md-6:last-child{
                        padding-left:1.5625rem;
                    }
                }
                img{
                    width: 100%;
                    margin-bottom:5.3125rem;
                }
                h3{
                    margin-bottom: 1.875rem;
                }
                .grid-container {
                    display: grid;
                    grid-template-columns: 9.6875rem auto;
                  }
                  .grid-item {
                    text-align: left;
                    padding-bottom: 2.1875rem;
                  }
            }
            .right{
                .mag-wrap{
                    width:33.75rem;
                    h1{
                        padding:3.125rem 2rem 6.25rem 0;
                    }
                    .display-2{
                        button{
                            margin-top:3.375rem;
                        }
                    }
                    .fr-infos-list{
                        span.display-4{
                            display: block;
                            margin-bottom: .5rem;
                        }
                        span.l-10{
                            display: block;
                            margin-bottom: 1.5rem;
                        }
                    }
                }
            }
        }
    }
    .big-cta{
        height: 95vh;
        position: relative;
        .wrapper{
            height: inherit;
            display: flex;
            align-items: center;
         .text-wraps{
             max-width: 25.9375rem;
         }
        }
    }
}