#info-page{
    .top-header{
        background: $black;
        overflow: hidden;
        position: relative;
        .wrapper{
            .row{
                .left{
                    padding: 10rem 0;
                    position: relative;
                    z-index: 1;
                    h1{
                        margin:3.125rem 0;
                        padding-right:7.5rem;
                    }
                    p{
                        padding-right:7.5rem;
                    }
                    a{
                        float:left;
                        margin-right: 1.5625rem;
                        margin-top:3.175rem;
                    }
                }    
                .right{
                    position: absolute;
                    right: 0;
                    top: 0;
                    display: flex;
                    align-items: flex-end;
                    padding: 0;
                    flex-direction: column;
                }
            }
        }
    }
    .blackline{
        padding-top:6.25rem;
        .big-wrapper{
            position: relative;
            .left{
                padding-bottom: 14.375rem;
                h1{
                    margin-bottom:3.125rem;
                }
                p{
                    padding-right: 15rem;
                }
            }
            .right{
                position: absolute;
                top:0;right:0;
                .col-md-4{
                    
                    overflow: hidden;
                    img{
                        background:#2d2d2d;
                    }
                }
                p{
                    padding-top:0.75rem;
                    padding-left: 15px;
                    font-weight: 600;
                }
            }
        }
    }
    .hip{
        background: $light-white;
        padding:12.5rem 0px;
        display: flex;
        flex-direction: row;
        justify-content: center;
        align-items: center;
        h1,img{
            margin-right: 1.875rem;
        }
    }
    .magazine{
        .wrapper{
            padding:5.625rem 0;
            .left{
                position: relative;
                .carousel{
                    position:initial;
                    img{
                        margin-bottom:3.125rem;
                    }
                    a.carousel-control-next{
                        position: absolute;
                        /* float: right; */
                        right: -8rem;
                        bottom: 0rem;
                        top: auto;
                    }
                    a.carousel-control-prev{
                        position: absolute;
                        /* left: auto; */
                        /* right: 10rem; */
                        /* float: right; */
                        bottom: 0rem;
                        top: auto;
                        right: -3rem;
                        left: auto;
                    }
                    
                }
            }
            .right{
                .mag-wrap{
                    width:33.75rem;
                    h1{
                        padding:3.125rem 0;
                    }
                    .display-2{
                        padding-right:2rem;
                        ul{
                            margin-top: 1.0625rem;
                            li{
                                padding:.5rem 0;
                            }
                        }
                        a.fr-button-link-brown-border{
                            margin-top:3.375rem;
                        }
                    }
                }
            }

        }
    }
    .magazine-rev{
        .wrapper{
            padding:5.625rem 0;
            .left{
                position: relative;
                .carousel{
                    position: initial;
                    img{
                        margin-bottom:3.125rem;
                    }
                    a.carousel-control-next{
                        position: absolute;
                        bottom: 0rem;
                        top: auto;
                        left: 5rem;
                    }
                    a.carousel-control-prev{
                        position: absolute;
                        /* left: auto; */
                        /* right: 10rem; */
                        /* float: right; */
                        bottom: 0rem;
                        top: auto;
                        left: 0;
                    }
                }

            }
            .right{
                .mag-wrap{
                    width:33.75rem;
                    h1{
                        padding:3.125rem 0;
                    }
                    .display-2{
                        padding-right:2rem;
                        ul{
                            margin-top: 1.0625rem;
                            li{
                                padding:.5rem 0;
                            }
                        }
                        a.fr-button-link-brown-border{
                            margin-top:3.375rem;
                        }
                    }
                }
            }
        }
    }
    .fem{
        .wrapper{
            padding:8.125rem 0;
            h4{
                padding-right:7rem;
                padding-bottom: 6.25rem;
            }
            .row-v{
                display: flex;
                flex-direction: row;
                .col-v{
                    width:20%;
                    display: flex;
                    flex-direction: column;
                    align-items: flex-start;
                    padding: 0 1.5rem;

                    img{
                        min-height:80px;
                        margin-left: auto;
                        margin-right: auto;
                    }
                    h3{
                        margin-top: 4.0625rem;
                        /* padding-bottom: 1.875rem; */
                        min-height: 4.75rem;
                        height: auto;
                    }
                }
            }
        }
    }
    .vision{
        background: $light-white;
           padding:6.25rem 0px;
           img{
               width:100%;
           }
       }
       .big-cta{
        height: 95vh;
        position: relative;
        .wrapper{
            height: inherit;
            display: flex;
            align-items: center;
         .text-wraps{
             max-width: 25.9375rem;
         }
        }
    }
}