#solution-page{
    .hero{
        background: $light-white;
        .wrapper{
            padding: 10rem 0 5rem 0;
            p{
                max-width:89rem;
                padding:3rem 0;
                color:$black;
            }
        }
    }
    .topic{
        .wrapper{
            padding:5rem 0;
        }
    }
    .features{
        height: 95vh;
        background: $light-white;
        .big-wrapper{
            height: inherit;
            padding:60px 0px;
            .accordion{
                .card{
                    .card-header{
                        background:rgba(0,0,0,.3);
                        button{
                            width:100%;
                            text-decoration: none;
                            text-align: left;
                            color:#000;
                            &:before{
                                float: right !important;
                                font-family: FontAwesome;
                                content:"\f068";
                                padding-right: 5px;
                            }
                            &.collapsed:before {
                                float: right !important;
                                font-family: FontAwesome;
                                content:"\f067";
                            }
                        }
                    }
                    .card-body{
                        padding:3rem;
                    }
                }
           }
            .row{
                height: 100%;
                display: flex;
                flex-direction: row;
                flex-wrap: nowrap;
                .col-md-3{
                    height: inherit;
                    overflow: hidden;
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    margin: 0px 1.5px;
                    padding:0px;
                    img{
                        margin-top:100px;
                      margin-left: -400px;
                    }
                    &:first-child img{
                        margin-top: 0px;
                        margin-left: 0px;
                    }
                    &:nth-child(2) img{
                     margin-left: -300px;
                 }
                    h1{
                        position: absolute;
                        bottom: 60px;
                        left:50px;
                        color:$white;
                    }
                }
            }
        }
    }
    .page-50{
        background:$brown;
        position: relative;
        height:45rem;
        .wrapper{
            height: 100%;
            .row{
                height: 100%;
                h1{
                    padding:5.4375rem 0;
                }
                .page-pic{
                    position: absolute;
                    right: 0;
                    top: 0;
                    padding: 0;
                    height: inherit;
                    overflow: hidden;
                    img{
                        float: right;
                        height: 100%;
                    }
                }
            }

        }
    }
    .inspire{
        .wrapper{
            padding:8.75rem 0;
            .left{
                display: flex;
                flex-direction: column;
                justify-content: center;
                h2{
                    padding:3.75rem 0;
                    max-width: 29.625rem;
                }
                p{
                    margin-bottom: 6.25rem;
                    max-width: 29.625rem;
                }
            }
            .right{
                svg{
                    position: absolute;
                    left: 50%;
                    //transform: translate(-1px,50px);
                }
                ul{
                    list-style: none;
                    padding: 0;
                    li{
                        display: flex;
                        flex-direction: row;
                        width:50%;
                        align-items: center;
                        position: relative;
                        height: 7.5rem;
                        .img-wrapper{
                            margin:0 2.5rem;
                            width:40px;
                        }
                        &:nth-child(odd){
                            left:50%;
                        }
                        &:nth-child(even){
                            flex-direction: row-reverse;
                            p{
                                text-align: right;
                                margin:0
                            }
                        }
                    }
                }
            }
        }
    }
    .cloud{
        margin-bottom: 9.0625rem;
        .big-wrapper{
            .row{
                .col-md-6{
                 padding:8.125rem;
                }
            }
        }
    }
    .big-cta{
        height: 95vh;
        position: relative;
        .wrapper{
            height: inherit;
            display: flex;
            align-items: center;
         .text-wraps{
             max-width: 25.9375rem;
         }
        }
    }
}