footer {
  background: $black;
  padding-top:8.125rem;

  .wrapper{

    .row{
      padding-bottom: 2.8125rem;
      h1,h5,a,i{
        color:$white;
      } 
      .left-footer{
        .fr-footer-flag{
          a{
            display: flex;
            flex-direction:row;
            opacity:.5;
            font-size: 0.9375rem;
            img{
              width:20px;
              height:20px;
              margin-right:0.625rem;
            }
            &.active{
              opacity:1;
              font-size: 1.25rem;
              img{
                width:25px;
                height:25px;
              }
            }
          }
        }
        .tab-pane{
          min-height: 23.68125rem;
        }
        .dropdown{
          display: flex;
          flex-direction: row;
          align-items: center;
          position: absolute;
        }
        h5{
          font-family: 'Work Sans', sans-serif;;
        }
        i{
          padding:0px 10px 0px 0px;
        }
        .col-sm-10{
          padding-left:3.125rem;
        }
      }
      .mid-footer{
          display: flex;
          justify-content: center;        
        .wraps{
          max-width:26.3125rem;
          p{
            padding-bottom: 1.5625rem;
          }
        }  
      }
      .end-footer{
        display: flex;
        flex-direction: column;
        align-items: center;
        img{
          margin-bottom: 5.375rem;
        }
        ul.list-v{
          li{
            width: fit-content;
            position: relative;
            a{
              &:after{
                content: '';
                display: block;
                height: 2px;
                width: 100%;
                background: #aa8c46;
                position: absolute;
                bottom: 2px;
                transform: all .3s ease-in-out;
              }
            }
            &:hover{
              a{
                &:after{
                  bottom: -2px;
                  transform: all .3s ease-in-out;
                }
              }
            }
          }
        }
        ul.social-links{
          display: flex;
          flex-direction: row;
          list-style: none;
          padding-left: 0;
          padding-top: 5.3125rem;
          .single-social{
            width:45px;
            height:45px;
            background:#333;
            border-radius:50px;
            margin-right:1.0625rem;
            display: flex;
            align-items: center;
            justify-content: center;
          }
        }
      }
    }

  }
  .copyright-footer{
    background: #222;
    font-family: 'Work Sans';
    font-size: 0.875rem;
    color:$white;
    padding:3.5625rem 0;
    span{
      display: block;
      text-align: right;
    }
  }
}
