#case-large-page{
    .two-sec{
        padding:6.25rem 0 3.4375rem 0;
        .page-desc{
            padding:0;
                .mag-wrap{
                    width:29.0625rem;
                    h1{
                        padding-bottom:3.125rem;
                    }
                    p{
                        padding-top:2.5125rem;
                    }
                    .display-2{
                        padding-right:2rem;
                    }
                }
        }
        .page-pic{
            display: flex;
            align-items: center;
            justify-content: flex-end;
            padding:0;

            video{
                max-width:51.625rem;
                width:100%;
                height:auto;
            }
        }
    }
    .mid-text{
        max-width:51.875rem;
        margin: 5rem auto;
        h1{
            margin-bottom: 3.4375rem;
        }
    }
    .two-sec-rev{
        padding:5.125rem 0;
        .page-desc{
            padding:0;
                .mag-wrap{
                    max-width:26.3125rem;
                    margin-left:auto;
                    h1{
                        padding-bottom:3.125rem;
                    }
                    p{
                        padding-top:2.5125rem;
                    }
                }
        }
        .page-pic{
            display: flex;
            align-items: center;
            justify-content: flex-end;
            padding:0;

            img{
                width:100%;
                height:auto;
            }
        }
    }
    .carousel{
        padding-top:5.5rem;
        height:63.625rem;
        overflow: hidden;
        img{
            width: 100%;
        }
        a.carousel-control-prev,a.carousel-control-next{
            
            top:50%;
            bottom: 65px;
            display: flex;
            opacity: 1;
            position: absolute;
            i{
                font-size: 2rem;
            }
        }
    }
    .small-text{
        p{
            text-align: center;
        }
        margin-bottom: 5rem;
    }
    .two-sec2{
        padding:5.125rem 0;
        .page-desc{
            padding:0;
                .mag-wrap{
                    max-width:26.3125rem;
                    margin-right:auto;
                    h1{
                        padding-bottom:3.125rem;
                    }
                    p{
                        padding-top:2.5125rem;
                    }
                    .display-2{
                        padding-right:1.5rem;
                    }
                }
        }
        .page-pic{
            display: flex;
            align-items: center;
            justify-content: flex-end;
            padding:0;
            img{
                width:100%;
                max-width:51.625rem;
                height:auto;
            }
        }
    }
    .only-img{
        padding:10.25rem 0 12rem 0;
        img{
            width:100%;
            height: auto;
        }
    }
    .vision{
        background: $light-grey;
           padding:6.25rem 0px;
           img{
               width:100%;
           }
       }
       .reference{
        background: $light-white;
        padding:5.625rem 0px;
        .wrapper{
            .row{
                height:6.75rem;
                display: flex;
                justify-content: center;
                align-items: center;
                .col-sm-2{
                    width:100%;
                    height: inherit;
                    display: flex;
                    justify-content: center;
                    align-items: center;
                    max-height: 100%;
                    img{
                        width:auto;
                        max-height: inherit;
                    }
                }
                &.button-center{
                    justify-content: flex-start;
                    margin-left: 45%;
                }
            }
        }
    }
    .big-cta{
        height: 95vh;
        position: relative;
        .wrapper{
            height: inherit;
            display: flex;
            align-items: center;
         .text-wraps{
             max-width: 25.9375rem;
         }
        }
    }
}