#news-main-page{
    .info-priser{
        padding:5rem 0;
        .row{
            h1{
                padding-bottom:3.75rem;
            }
            p{
                padding-right:2rem;
                margin-bottom:3.125rem;
            }
            .col-md-6{
                padding:0;
                margin-bottom:3.0625rem;
            }
            .cont{
                padding-right:9.375rem;
            }
            .pic{
                display: flex;
                justify-content: flex-end;
                align-items: flex-start;
                padding-top: 6.875rem;
            }
        }
    }
    .big-cta{
        height: 95vh;
        position: relative;
        .wrapper{
            height: inherit;
            display: flex;
            align-items: center;
         .text-wraps{
             max-width: 25.9375rem;
         }
        }
    }
}